.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: calc(100vw - (19px * 2));
  padding: 0 19px;
  margin: 0 auto;
}
.body {
  min-height: 100%;
  margin: 0;
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  background-color: #ffffff;
  color: #333333;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  overflow-x: clip;
}
.main-header {
  min-height: 56px;
  position: relative;
  padding: 17px 0px;
  box-sizing: border-box;
}
.main-header__button {
  background: none;
  border: none;
  padding: 0;
  width: 23px;
  height: 22px;
  background: url("../../img/icon-menu.svg.svg");
  cursor: pointer;
  margin-left: auto;
  display: block;
}
.main-menu__list {
  display: none;
}
.main-menu__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
}
.first-screen__main-text {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 35px;
  color: #000000;
}
.first-screen__text {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: #333333;
}
.first-screen {
  padding-bottom: 16px;
}
.first-screen__img {
  margin-bottom: 32px;
}
.benefits__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
